// tests/e2e/factures/factureWorkflow.test.jsx
// Tests End-to-End pour le workflow complet de gestion des factures

import React from 'react';
import { renderWithProviders, screen, waitFor, fillForm, submitForm } from '../../utils/testUtils';
import { mockFactureService, mockClientService, mockTarificationService, resetAllMocks } from '../../mocks/services';

// Mocks
jest.mock('@services/FactureService', () => {
  return jest.fn().mockImplementation(() => mockFactureService);
});

jest.mock('@services/ClientService', () => {
  return jest.fn().mockImplementation(() => mockClientService);
});

jest.mock('@services/TarificationService', () => {
  return jest.fn().mockImplementation(() => mockTarificationService);
});

// Import après les mocks
import FactureGestion from '@components/factures/FactureGestion';

describe('Workflow Factures E2E', () => {
  beforeEach(() => {
    resetAllMocks();
    jest.clearAllMocks();
  });

  describe('Workflow complet : Création → Modification → Suppression', () => {
    it('devrait permettre de créer une nouvelle facture', async () => {
      const { user } = renderWithProviders(<FactureGestion />);
      
      // 1. Attendre le chargement initial
      await waitFor(() => {
        expect(screen.queryByText(/chargement/i)).not.toBeInTheDocument();
      });
      
      // 2. Cliquer sur "Nouvelle facture"
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      // 3. Vérifier que le formulaire s'affiche
      await waitFor(() => {
        expect(screen.getByText(/nouvelle facture/i)).toBeInTheDocument();
      });
      
      // 4. Remplir le formulaire
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // 5. Ajouter une ligne
      const addLineButton = screen.getByRole('button', { name: /ajouter.*ligne/i });
      await user.click(addLineButton);
      
      // 6. Remplir la ligne
      await waitFor(() => {
        expect(screen.getByTestId('ligne-facture-0')).toBeInTheDocument();
      });
      
      const serviceSelect = screen.getByTestId('service-ligne-0');
      await user.selectOptions(serviceSelect, '1');
      
      const quantiteInput = screen.getByTestId('quantite-ligne-0');
      await user.clear(quantiteInput);
      await user.type(quantiteInput, '2');
      
      // 7. Soumettre
      await submitForm(user, 'Enregistrer');
      
      // 8. Vérifier le succès
      await waitFor(() => {
        expect(mockFactureService.creerFacture).toHaveBeenCalled();
        expect(screen.getByText(/facture.*créée/i)).toBeInTheDocument();
      });
    });

    it('devrait permettre de modifier une facture existante', async () => {
      // Préparer les mocks
      mockFactureService.chargerFactures.mockResolvedValue({
        success: true,
        factures: [createTestFacture({ idFacture: 1, etat: 'En attente' })]
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      // 1. Attendre le chargement
      await waitFor(() => {
        expect(screen.getByText('001.2025')).toBeInTheDocument();
      });
      
      // 2. Cliquer sur Modifier
      const editButton = screen.getByRole('button', { name: /modifier/i });
      await user.click(editButton);
      
      // 3. Vérifier que le formulaire s'affiche en mode édition
      await waitFor(() => {
        expect(screen.getByDisplayValue('001.2025')).toBeInTheDocument();
      });
      
      // 4. Modifier la ristourne
      const ristourneInput = screen.getByLabelText(/ristourne/i);
      await user.clear(ristourneInput);
      await user.type(ristourneInput, '50');
      
      // 5. Sauvegarder
      await submitForm(user, 'Modifier');
      
      // 6. Vérifier le succès
      await waitFor(() => {
        expect(mockFactureService.modifierFacture).toHaveBeenCalledWith(1, expect.objectContaining({
          ristourne: 50
        }));
      });
    });

    it('devrait permettre de supprimer une facture', async () => {
      mockFactureService.chargerFactures.mockResolvedValue({
        success: true,
        factures: [createTestFacture({ idFacture: 1 })]
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      // 1. Attendre le chargement
      await waitFor(() => {
        expect(screen.getByText('001.2025')).toBeInTheDocument();
      });
      
      // 2. Cliquer sur Supprimer
      const deleteButton = screen.getByRole('button', { name: /supprimer/i });
      await user.click(deleteButton);
      
      // 3. Confirmer la suppression
      await waitFor(() => {
        expect(screen.getByText(/confirmer.*suppression/i)).toBeInTheDocument();
      });
      
      const confirmButton = screen.getByRole('button', { name: /confirmer/i });
      await user.click(confirmButton);
      
      // 4. Vérifier le succès
      await waitFor(() => {
        expect(mockFactureService.supprimerFacture).toHaveBeenCalledWith(1);
        expect(screen.getByText(/facture.*supprimée/i)).toBeInTheDocument();
      });
    });
  });

  describe('Workflow : Visualisation et actions', () => {
    it('devrait permettre de visualiser une facture', async () => {
      mockFactureService.chargerFactures.mockResolvedValue({
        success: true,
        factures: [createTestFacture({ idFacture: 1 })]
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      await waitFor(() => {
        expect(screen.getByText('001.2025')).toBeInTheDocument();
      });
      
      // Cliquer sur la ligne ou le bouton Voir
      const viewButton = screen.getByRole('button', { name: /voir/i });
      await user.click(viewButton);
      
      // Vérifier l'affichage en mode lecture
      await waitFor(() => {
        const inputs = screen.getAllByRole('textbox');
        inputs.forEach(input => {
          expect(input).toHaveAttribute('readonly');
        });
      });
    });

    it('devrait permettre d\'imprimer depuis la visualisation', async () => {
      mockFactureService.chargerFactures.mockResolvedValue({
        success: true,
        factures: [createTestFacture({ idFacture: 1 })]
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      await waitFor(() => {
        expect(screen.getByText('001.2025')).toBeInTheDocument();
      });
      
      // Aller en mode visualisation
      const viewButton = screen.getByRole('button', { name: /voir/i });
      await user.click(viewButton);
      
      // Imprimer
      await waitFor(() => {
        expect(screen.getByRole('button', { name: /imprimer/i })).toBeInTheDocument();
      });
      
      const printButton = screen.getByRole('button', { name: /imprimer/i });
      await user.click(printButton);
      
      expect(mockFactureService.imprimerFacture).toHaveBeenCalledWith(1);
    });
  });

  describe('Workflow : Filtrage et recherche', () => {
    it('devrait filtrer et afficher les résultats corrects', async () => {
      mockFactureService.chargerFactures.mockResolvedValue({
        success: true,
        factures: [
          createTestFacture({ idFacture: 1, etat: 'En attente' }),
          createTestFacture({ idFacture: 2, etat: 'Payée' }),
          createTestFacture({ idFacture: 3, etat: 'En attente' }),
        ]
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      await waitFor(() => {
        expect(screen.getAllByRole('row').length).toBeGreaterThan(3);
      });
      
      // Filtrer par état "Payée"
      const etatFilter = screen.getByLabelText(/état/i);
      await user.selectOptions(etatFilter, 'Payée');
      
      // Vérifier que seule la facture payée est affichée
      await waitFor(() => {
        expect(screen.getAllByRole('row').length).toBe(2); // Header + 1 facture
      });
    });
  });

  describe('Workflow : Gestion des erreurs', () => {
    it('devrait gérer une erreur de création', async () => {
      mockFactureService.creerFacture.mockRejectedValueOnce(new Error('Erreur serveur'));
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      // Aller au formulaire de création
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      // Remplir minimalement
      await waitFor(() => {
        expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
      });
      
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // Soumettre
      await submitForm(user, 'Enregistrer');
      
      // Vérifier l'erreur
      await waitFor(() => {
        expect(screen.getByText(/erreur/i)).toBeInTheDocument();
      });
    });

    it('devrait permettre de réessayer après une erreur', async () => {
      // Première tentative échoue
      mockFactureService.creerFacture.mockRejectedValueOnce(new Error('Erreur'));
      
      // Deuxième tentative réussit
      mockFactureService.creerFacture.mockResolvedValueOnce({
        success: true,
        idFacture: 1
      });
      
      const { user } = renderWithProviders(<FactureGestion />);
      
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      await waitFor(() => {
        expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
      });
      
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      // Première soumission (échec)
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(screen.getByText(/erreur/i)).toBeInTheDocument();
      });
      
      // Réessayer
      await submitForm(user, 'Enregistrer');
      
      await waitFor(() => {
        expect(mockFactureService.creerFacture).toHaveBeenCalledTimes(2);
      });
    });
  });

  describe('Workflow : Navigation', () => {
    it('devrait revenir à la liste après création', async () => {
      const { user } = renderWithProviders(<FactureGestion />);
      
      // Aller au formulaire
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      await waitFor(() => {
        expect(screen.getByLabelText(/client/i)).toBeInTheDocument();
      });
      
      // Remplir et soumettre
      const clientSelect = screen.getByLabelText(/client/i);
      await user.selectOptions(clientSelect, '1');
      
      await submitForm(user, 'Enregistrer');
      
      // Vérifier le retour à la liste
      await waitFor(() => {
        expect(screen.getByRole('table')).toBeInTheDocument();
      });
    });

    it('devrait revenir à la liste avec le bouton Retour', async () => {
      const { user } = renderWithProviders(<FactureGestion />);
      
      // Aller au formulaire
      const newButton = screen.getByRole('button', { name: /nouvelle facture/i });
      await user.click(newButton);
      
      // Cliquer sur Retour
      const retourButton = screen.getByRole('button', { name: /retour/i });
      await user.click(retourButton);
      
      // Vérifier le retour à la liste
      await waitFor(() => {
        expect(screen.getByRole('table')).toBeInTheDocument();
      });
    });
  });
});